const body = document.querySelector("body");
const tree = document.querySelector(".tree");
const bird = document.querySelector(".bird");
const form = document.querySelector("form");

form.addEventListener("submit", (event) => {
  event.preventDefault();

  const maxWidth = document.body.clientWidth;
  const maxHeight = document.body.clientHeight;

  const randomTree = () => {
    const className = `tree-${Math.ceil(Math.random() * 3)}`;
    console.log(className);
    const div = document.createElement("div");
    div.setAttribute("class", className);
    div.style.top = `${Math.floor(Math.random() * maxHeight + 1)}px`;
    div.style.left = `${Math.floor(Math.random() * maxWidth + 1)}px`;

    body.appendChild(div);
  };

  for (let i = 0; i <= tree.value; i++) {
    randomTree();
  }

  const randomInsect = () => {
    const div = document.createElement("div");
    div.setAttribute("class", "insect");
    div.classList.add("fly");
    div.style.width = "90px";
    div.style.height = "70px";

    div.style.top = `${Math.floor(Math.random() * maxHeight + 1)}px`;
    div.style.left = `${Math.floor(Math.random() * maxWidth + 1)}px`;

    body.appendChild(div);
  };

  for (let i = 0; i <= bird.value; i++) {
    randomInsect();
  }
});
